.splash-container {
  width: 80%;
  margin: auto;
  padding: 5rem;
}

.main-page-text {
  width: 80%;
  margin: auto;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

h1 {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 1rem;
  padding: 1rem;
}

p {
  font-size: 1.5rem;
  margin-bottom: 2rem;
  padding: 1rem;
}

button {
  padding: 1rem 2rem;
  font-size: 1.2rem;
  font-weight: bold;
  color: white;
  background-color: #333;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #555;
}

@media (max-width: 600px) {
  .splash-container {
    padding: 2rem;
    padding: 2rem 1rem;
    width: 100%;
  }

  .splash-container p {
   text-align: justify;
  }
}
